<template>
  <div class="main">
        <div class="box">
          <el-form
            inline
            :model="form"
            label-width="130px"
            ref="Form"
            label-position="right"
            :rules="rules"
          >
            <div class="boxTitle">基本信息</div>
            <el-form-item label="姓名">
              <el-input size="small" v-model="form.userName" disabled></el-input>
            </el-form-item>
            <el-form-item label="身份证号">
              <el-input size="small" v-model="form.userIdentity" disabled></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="userSex" >
              <el-radio-group v-model="form.userSex">
                <el-radio label="男" value="男"></el-radio>
                <el-radio label="女" value="女"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="民族" prop="userFamousRaceId" >
              <el-select size="small" v-model="form.userFamousRaceId" filterable placeholder="民族">
                <el-option
                  v-for="item in userFamousRaceList"
                  :key="item.famousRaceId"
                  :label="item.famousRaceName"
                  :value="item.famousRaceId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="出生日期" prop="userBirthday" >
              <el-date-picker
                size="small"
                v-model="form.userBirthday"
                type="date"
                placeholder="选择日期"
                value-format="yyyy/MM/dd"
                format="yyyy 年 MM 月 dd 日"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="户口性质" prop="accountCharacterId" >
              <el-select
                size="small"
                v-model="form.accountCharacterId"
                filterable
                placeholder="请选择户口性质"
              >
                <el-option
                  v-for="item in accountCharacterList"
                  :key="item.accountCharacterId"
                  :label="item.accountCharacterName"
                  :value="item.accountCharacterId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="毕业中学名称" prop="userMiddleSchool" >
              <el-input size="small" v-model="form.userMiddleSchool" placeholder="请填写毕业中学名称"></el-input>
            </el-form-item>
            <el-form-item label="资助中心" style="display:block" prop="fundingCenterId" >
              <el-select
                size="small"
                style="width:690px"
                v-model="form.fundingCenterId"
                filterable
                placeholder="请选择资助中心"
              >
                <el-option
                  v-for="item in fundingCenterList"
                  :key="item.fundingCenterId"
                  :label="item.fundingCenterName"
                  :value="item.fundingCenterId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label="入学前户籍地址"
              prop="userPermanentAddress"
              style="display:block"
              required
            >
              <div class="selectGround">
                <el-select
                  size="small"
                  v-model="form.provinceId"
                  filterable
                  placeholder="请选择省份"
                  @change="getCity('provinceId')"
                >
                  <el-option
                    v-for="item in provinceList"
                    :key="item.provinceId"
                    :label="item.provinceName"
                    :value="item.provinceId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.cityId"
                  filterable
                  placeholder="请选择城市"
                  @change="getArea('cityId')"
                >
                  <el-option
                    v-for="item in cityList"
                    :key="item.cityId"
                    :label="item.cityName"
                    :value="item.cityId"
                  ></el-option>
                </el-select>
                <el-select size="small" v-model="form.areaId" filterable placeholder="请选择区域">
                  <el-option
                    v-for="item in areaList"
                    :key="item.areaId"
                    :label="item.areaName"
                    :value="item.areaId"
                  ></el-option>
                </el-select>
              </div>
              <el-input
                style="width:690px"
                size="small"
                v-model="form.userPermanentAddress"
                placeholder="请填写详细的户籍地址"
              ></el-input>
            </el-form-item>
            <el-form-item label="银行卡号" prop="userBankCardId" >
              <el-input size="small" type="number" v-model="form.userBankCardId" placeholder="请填写银行卡号"></el-input>
            </el-form-item>
            <div class="boxTitle">通讯信息</div>
            <el-form-item label="手机" style="display:block" >
              <el-input size="small" type="number" v-model="form.userPhone" placeholder="请填写手机" disabled></el-input>
            </el-form-item>
            <el-form-item prop="userPostalCode" label="邮政编码" >
              <el-input size="small" type="number" v-model="form.userPostalCode" placeholder="请填写邮政编码"></el-input>
            </el-form-item>
            <el-form-item label="电子邮件">
              <el-input size="small" v-model="form.userMailBox" placeholder="请填写电子邮件"></el-input>
            </el-form-item>
            <el-form-item prop="userQq" label="QQ" >
              <el-input size="small" type="number" v-model="form.userQq" placeholder="请填写QQ"></el-input>
            </el-form-item>
            <el-form-item label="微信">
              <el-input size="small" v-model="form.userWeChat" placeholder="请填写微信"></el-input>
            </el-form-item>
            <el-form-item label="通讯地址">
              <el-input
                size="small"
                style="width:690px"
                v-model="form.userPostalAddress"
                placeholder="请填写详细地址，填写到村镇、门牌号"
              ></el-input>
            </el-form-item>
            <el-form-item prop="userFamilyPhone" label="家庭电话" style="display:block" >
              <el-input size="small" v-model="form.userFamilyPhone" placeholder="请填写家庭电话"></el-input>
            </el-form-item>

            <div class="boxTitle">联系人信息</div>
            <el-form-item prop="userContactName" label="姓名" >
              <el-input size="small" v-model="form.userContactName" placeholder="请填写姓名"></el-input>
            </el-form-item>
            <el-form-item prop="userContactPhone" label="联系电话" >
              <el-input size="small" type="number" v-model="form.userContactPhone" placeholder="请填写联系电话"></el-input>
            </el-form-item>
            <el-form-item prop="userContactIdentity" label="联系人身份证号" >
              <el-input size="small" v-model="form.userContactIdentity" placeholder="请填写联系人身份证号"></el-input>
            </el-form-item>
            <div class="boxTitle">就学信息</div>
            <el-form-item prop="userUniversity" label="高校名称" required style="display:block">
              <el-input size="small" v-model="form.userUniversity" placeholder="请填写高校名称"></el-input>
            </el-form-item>
            <el-form-item prop="userDepartment" label="院系名称" >
              <el-input size="small" v-model="form.userDepartment" placeholder="请填写院系名称"></el-input>
            </el-form-item>
            <el-form-item prop="userMajor" label="专业名称" >
              <el-input size="small" v-model="form.userMajor" placeholder="请填写专业名称"></el-input>
            </el-form-item>
            <el-form-item prop="userEducation" label="学历" >
              <el-select size="small" v-model="form.userEducation" filterable placeholder="请填写学历">
                <el-option
                  v-for="(item,index) in userEducationList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userEnrollmentYear" label="入学年份" >
              <el-date-picker
                prefix-icon
                size="small"
                v-model="form.userEnrollmentYear"
                type="year"
                placeholder="请填写入学年份"
                value-format="yyyy"
                format="yyyy 年"
              ></el-date-picker>
            </el-form-item>
            <el-form-item prop="userStudentId" label="学号">
              <el-input size="small" v-model="form.userStudentId" placeholder="请填写学号"></el-input>
            </el-form-item>
            <el-form-item prop="userMajorCategory" label="专业类别" >
              <el-select
                size="small"
                v-model="form.userMajorCategory"
                filterable
                placeholder="请填写专业类别"
              >
                <el-option
                  v-for="(item,index) in userMajorCategoryList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userEducationalSystem" label="学制" >
              <el-select
                size="small"
                v-model="form.userEducationalSystem"
                filterable
                placeholder="请填写学制"
              >
                <el-option
                  v-for="(item,index) in userEducationalSystemList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userGraduationTime" label="毕业时间" >
              <el-date-picker
                size="small"
                v-model="form.userGraduationTime"
                type="date"
                placeholder="选择毕业时间"
                value-format="yyyy/MM/dd"
                format="yyyy 年 MM 月 dd 日"
              ></el-date-picker>
            </el-form-item>
            <div class="boxTitle">共同贷款人信息</div>
            <el-form-item prop="guardianName" label="姓名" >
              <el-input size="small" v-model="form.guardianName" placeholder="请填写姓名"></el-input>
            </el-form-item>
            <el-form-item prop="guardianIdentity" label="身份证号" >
              <el-input size="small" type="number" v-model="form.guardianIdentity" placeholder="请填写身份证号"></el-input>
            </el-form-item>
            <el-form-item
              prop="guardianPermanentAddress"
              label="户籍地址"
              style="display:block"
              required
            >
              <div class="selectGround">
                <el-select
                  size="small"
                  v-model="form.guardianProvinceId"
                  filterable
                  placeholder="请选择省份"
                  @change="getCity('guardianProvinceId')"
                >
                  <el-option
                    v-for="item in provinceList"
                    :key="item.provinceId"
                    :label="item.provinceName"
                    :value="item.provinceId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.guardianCityId"
                  filterable
                  placeholder="请选择城市"
                  @change="getArea('guardianCityId')"
                >
                  <el-option
                    v-for="item in cityList"
                    :key="item.cityId"
                    :label="item.cityName"
                    :value="item.cityId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.guardianAreaId"
                  filterable
                  placeholder="请选择区域"
                >
                  <el-option
                    v-for="item in areaList"
                    :key="item.areaId"
                    :label="item.areaName"
                    :value="item.areaId"
                  ></el-option>
                </el-select>
              </div>
              <el-input
                style="width:690px"
                size="small"
                v-model="form.guardianPermanentAddress"
                placeholder="请填写详细的户籍地址"
              ></el-input>
            </el-form-item>
            <el-form-item prop="guardianPostalCode" label="邮政编码" >
              <el-input size="small" type="number" v-model="form.guardianPostalCode" placeholder="请填写邮政编码"></el-input>
            </el-form-item>
            <el-form-item prop="guardianTel" label="家庭电话" >
              <el-input size="small" type="number" v-model="form.guardianTel" placeholder="请填写家庭电话"></el-input>
            </el-form-item>
            <el-form-item prop="guardianHealth" label="身体健康状况" >
              <el-radio-group v-model="form.guardianHealth">
                <el-radio label="健康" value="健康"></el-radio>
                <el-radio label="患病" value="患病"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item prop="relation" label="与学生关系" >
              <el-input size="small" v-model="form.relation" placeholder="请填写与学生关系"></el-input>
            </el-form-item>
            <el-form-item class="submit">
               <el-button type="primary" @click="submit">提交</el-button>
            </el-form-item>
          </el-form>
         
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      accountCharacterList: [],
      fundingCenterList: [],
      provinceList: [],
      cityList: [],
      areaList: [],
      userEducationList: ["专科", "本科", "研究生", "硕士", "博士"],
      userEducationalSystemList: ["3年", "4年", "6年", "8年"],
      userMajorCategoryList: [
        "哲学",
        "经济学",
        "法学",
        "教育学",
        "文学",
        "历史学",
        "理学",
        "工学",
        "农学",
        "医学",
        "军事学",
        "管理学"
      ],
      userFamousRaceList: [],
      form: {
        userName: "",
        userIdentity: "",
        userSex: "",
        userBirthday: "",
        userFamousRaceId: "",
        userMiddleSchool: "",
        accountCharacterId: "",
        fundingCenterId: "",
        provinceId: "",
        cityId: "",
        areaId: "",
        userPermanentAddress: "",
        userBankCardId: "",

        userPhone: "",
        userPostalCode: "",
        userMailBox: "",
        userQq: "",
        userWeChat: "",
        userPostalAddress: "",
        userFamilyPhone: "",

        userContactName: "",
        userContactPhone: "",
        userContactIdentity: "",

        userUniversity: "",
        userDepartment: "",
        userMajor: "",
        userEducation: "",
        userEnrollmentYear: "",
        userStudentId: "",
        userMajorCategory: "",
        userEducationalSystem: "",
        userGraduationTime: "",

        guardianName: "",
        guardianIdentity: "",
        guardianProvinceId: "",
        guardianCityId: "",
        guardianAreaId: "",
        guardianPermanentAddress: "",
        guardianPostalCode: "",
        guardianHealth: "",
        guardianTel: "",
        relation: ""
      },
      rules: {
        userSex: [{ required: true, message: "请选择性别", trigger: "blur" }],
        userBirthday: [
          { required: true, message: "请选择出生日期", trigger: "blur" }
        ],
        userFamousRaceId: [
          { required: true, message: "请选择民族", trigger: "blur" }
        ],
        userMiddleSchool: [
          { required: true, message: "请填写毕业中学", trigger: "blur" }
        ],
        accountCharacterId: [
          { required: true, message: "请选择户口性质", trigger: "blur" }
        ],
        fundingCenterId: [
          { required: true, message: "请选择资助中心", trigger: "blur" }
        ],
        userPermanentAddress: [
          { required: true, message: "请填写完整地址", trigger: "blur" }
        ],
        userBankCardId: [
          { required: true, message: "请填写银行卡号", trigger: "blur" }
        ],

        userPostalCode: [
          { required: true, message: "请填写邮政编码", trigger: "blur" }
        ],
        userQq: [{ required: true, message: "请填写QQ", trigger: "blur" }],
        userFamilyPhone: [
          { required: true, message: "请填写家庭电话", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error("电话格式错误"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],

        userContactName: [
          { required: true, message: "请填写姓名", trigger: "blur" }
        ],
        userContactPhone: [
          { required: true, message: "请填写联系电话", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error("电话格式错误"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        userContactIdentity: [
          { required: true, message: "请填写联系人身份证号", trigger: "blur" },
          { min: 15, max: 18, message: "身份证不合法", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "身份证不合法",
            trigger: "blur"
          }
        ],

        userUniversity: [
          { required: true, message: "请填写高校名称", trigger: "blur" }
        ],
        userDepartment: [
          { required: true, message: "请填写院系名称", trigger: "blur" }
        ],
        userMajor: [{ required: true, message: "请填写专业", trigger: "blur" }],
        userEducation: [
          { required: true, message: "请选择学历", trigger: "blur" }
        ],
        userEnrollmentYear: [
          { required: true, message: "请选择入学年份", trigger: "blur" }
        ],
        userMajorCategory: [
          { required: true, message: "请选择专业类别", trigger: "blur" }
        ],
        userEducationalSystem: [
          { required: true, message: "请选择学制", trigger: "blur" }
        ],
        userGraduationTime: [
          { required: true, message: "请选择毕业时间", trigger: "blur" }
        ],

        guardianName: [
          { required: true, message: "请填写姓名", trigger: "blur" }
        ],
        guardianIdentity: [
          { required: true, message: "请填写身份证", trigger: "blur" },
          { min: 15, max: 18, message: "身份证不合法", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "身份证不合法",
            trigger: "blur"
          }
        ],
        guardianPermanentAddress: [
          { required: true, message: "请填写完整地址", trigger: "blur" }
        ],
        guardianPostalCode: [
          { required: true, message: "请填写邮政编码", trigger: "blur" }
        ],
        guardianHealth: [
          { required: true, message: "请选择健康状态", trigger: "blur" }
        ],
        guardianTel: [
          { required: true, message: "请填写家庭电话", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error("手机号格式错误"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        relation: [
          { required: true, message: "请填写与学生关系", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.$axios
      .post("/user/getUserInfo", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          this.form.userName = data.user.userName;
          this.form.userIdentity = data.user.userIdentity;
          this.form.userPhone = data.user.userPhone;
        }
      });
    this.$axios
      .post("/famousRace/getAllFamousRace")
      .then(({ status, data }) => {
        if (status == 200) {
          this.userFamousRaceList = data;
        }
      });
    this.$axios
      .post("/accountCharacter/getAllAccountCharacter")
      .then(({ status, data }) => {
        if (status == 200) {
          this.accountCharacterList = data;
        }
      });
    this.$axios
      .post("/fundingCenter/getFundingCenterList")
      .then(({ status, data }) => {
        if (status == 200) {
          this.fundingCenterList = data;
        }
      });
    this.$axios.post("/province/getAllProvince").then(({ status, data }) => {
      if (status == 200) {
        this.provinceList = data;
      }
    });
  },
  methods: {
    getCity(key) {
      this.$axios
        .post("/city/getAllCityByProvinceId", {
          provinceId: this.form[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.cityList = data;
          }
        });
    },
    getArea(key) {
      this.$axios
        .post("/area/getAllAreaByCityId", {
          cityId: this.form[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.areaList = data;
          }
        });
    },
    submit() {
      
      this.$refs.Form.validate(valid => {
        if (valid) {
          this.$axios
            .post("/user/improveUserInfo", {
              userLoginAccount: sessionStorage.getItem("userLoginAccount"),
              ...this.form
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "用户信息完善成功",
                  type: "success"
                });
                this.$emit('close')
              } else if (status == 401) {
                this.$message({
                  message: "用户信息完善失败",
                  type: "error"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.main {
  min-width: 700px;
  max-width: 1000px;
  background: #fff;
  .box {
    padding: 15px 30px;
    .boxTitle {
      text-align: left;
      color: rgb(0, 158, 223);
      font-size: 16px;
      font-weight: bold;
    }
    .boxTitle:not(:first-child) {
      margin-top: 30px;
    }
    .el-form-item {
      margin-bottom: 13px;
      /deep/ .el-form-item__label {
        font-size: 14px;
        font-weight: bold;
      }
      /deep/ .el-form-item__content {
        width: 275px;
        text-align: left;
      }
      
      .selectGround {
        display: flex;
        width: 690px;
        /deep/ .el-select{
          width:250px;
        }
        /deep/ .el-select:nth-child(2), .el-select:nth-child(5) {
